a {
  color: black;
}

.Contact {
  padding: 20vh 0 5vh;
}

.ProjectSectionSvgIcons {
  margin: 0 5vw 5vh 1vw;
  transform: scale(1.8);
  float: 5px;
}

/* ApexCharts*/

body {
  background-color: white;
  color: #777;
  font-family: "Titillium Web", Arial, Helvetica, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
strong {
  font-weight: 600;
}

p {
  margin-bottom: 8px;
}

.ContactSection {
  width: 300px;
  padding-bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.ProjectSectionSvgIcons {
  margin: 0 10px;
  display: inline-block;
  vertical-align: middle;
}